.page-home, .page-favorites {
  position: relative;
}

.page-home:hover, .page-home:focus, .page-favorites:hover, .page-favorites:focus {
  color: var(--accent-color);
  transition: color var(--animation-set);
}

.page-home:before, .page-favorites:before {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--accent-color);
  transition: transform var(--animation-set);
  border-radius: 4px;
  display: block;
  position: absolute;
  top: -2px;
  left: 0;
  transform: scale(0);
}

.page-home:after, .page-favorites:after {
  content: "";
  width: 100%;
  height: 1.8px;
  background-color: var(--accent-color);
  transition: transform var(--animation-set);
  border-radius: 4px;
  display: block;
  position: absolute;
  bottom: -2px;
  left: 0;
  transform: scale(0);
}

.page-home:hover:before, .page-home:hover:after, .page-favorites:hover:before, .page-favorites:hover:after, .page-home:focus:before, .page-home:focus:after, .page-favorites:focus:before, .page-favorites:focus:after {
  transform: scale(1);
}

.page-navigation .current {
  color: var(--accent-color);
}

.shopping-link:hover .shopping-icon, .shopping-link:focus .shopping-icon {
  stroke: var(--accent-color);
  transition: var(--animation-set);
}

.mobile-container {
  z-index: 666;
  width: 200px;
  height: 100vh;
  background-color: var(--accent-color);
  transform-origin: bottom;
  transition: transform 1000ms var(--cubic);
  margin-bottom: auto;
  padding-top: 200px;
  padding-left: 73px;
  padding-right: 74px;
  position: fixed;
  top: 0;
  right: 0;
  transform: scaleY(0);
}

.mobile-container.is-open {
  transform-origin: top;
  transition: transform 1000ms var(--cubic);
  transform: scaleY(1);
}

.mobile-nav-link {
  color: var(--secondary-text-color);
  text-align: center;
